<template>
    <div class="setOrder">
      <header class="count_top header">
        <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
        <span>确认订单</span>
      </header>
      <div @click="selectAddress">
        <section v-if="defaultAddress === null" class="order_w">
          <img src="../../assets/person/address.png" alt=""  width="50">
          <span>请添加收货地址</span>
        </section>
        <section class="order_address_t" v-else>
          <div>
            <img src="../../assets/person/address.png" alt="" >          </div>
          <div>
              <section class="address_title">
                <p>收件人: {{defaultAddress.username}}</p>
                <p>{{defaultAddress.telphone}}</p>
              </section>
              <p>
                收货地址: <span v-for="item in defaultAddress.city">{{item.name}}</span>{{defaultAddress.address}}
              </p>
          </div>
          <div>
            <img src="../../assets/shopImage/right.png" alt="">
          </div>
        </section>
      </div>
      <ul>
        <li class="order_good" @click="back" v-for="(item, key) in goodsList">
          <section>
            <img :src="item.pic_url" alt="">
          </section>
          <section>
            <p class="good_name">{{item.good_name}}</p>
            <div class="good_num">
              <p class="good_price">¥ {{item.good_price}}</p>
              <p>x {{item.good_num}}</p>
            </div>
          </section>
        </li>
      </ul>
      <label class="site">
        <span>买家留言:</span>
        <input type="text" placeholder="选填,可填写和卖家达成一致的要求" v-model="content">
      </label>
      <section class="goods_price">
        <span>运费</span>
        <span>¥{{all}}</span>
      </section>
      <section class="goods_price">
        <span>订单总价</span>
        <span>¥{{totalPrice}}</span>
      </section>
      <section class="goods_price">
        <span>需付款</span>
        <span class="price_d">¥{{totalPrice}}</span>
      </section>
      <footer class="order_btn">
        <section class="btn_l">
          <span>合计:</span>
          <span>¥{{totalPrice + all}}</span>
        </section>
        <section class="btn_r" @click="submitOrder">
          提交订单
        </section>
      </footer>
      <pop ref="pop" @show="payType"></pop>
    </div>
</template>
<script>
  import { Toast} from 'mint-ui';
  import { Indicator} from 'mint-ui';
  import pop from  '../common/pop'
export default {
  data(){
    return {
      defaultAddress: null,
      goodsList: [],
      addressId: '',
      content:'',
      totalPrice: 0,
      payInfo: {},
      all:0
    }
  },
  methods:{
    payType(val) {
      //0 余额 1 其他
      if (val === '0') {
        this.post('home/Pay/payForMoney', {
          id: this.payInfo.id,
          type: this.payInfo.type
        }).then(res => {
          if (res.data.code === 10000){
            this.$router.push('/info');
            Toast({
              message: res.data.info,
              duration: 1000
            });
          } else {
            this.$router.push('/info');
            Toast({
              message: res.data.info,
              duration: 1000
            });
          }
        })
      } else {
        let token = localStorage.getItem('token');
        location.href = `http://flm.yunkzx.com/home/Shop/order_pay?token=${token}&id=${this.payInfo.id}&type=${this.payInfo.type}`;
      }
    },
    //获取商品信息
    getOrderList(id){
      this.post('home/Shop/showAllShop', {
          idList: id
      }).then(res => {
        if (res.data.code === 10000){
          this.defaultAddress = res.data.info.address;
          if (this.defaultAddress !== null){
            this.addressId = res.data.info.address.address_id;
            this.defaultAddress.city = JSON.parse(res.data.info.address.city);
          }
          this.goodsList = res.data.info.data;
          this.goodsList.forEach(item => {
            item.pic_url = this.imageURL(item.pic_url);
            this.totalPrice += item.good_price * item.good_num;
            this.all += (item.yunfei - 0)
          })
        }
      })
    },
    submitOrder(){
      this.$refs.pop.value = '';
        let list = [];
        this.goodsList.forEach(item => {
          let obj = {};
            obj['good_id']= item.id;
            obj['number'] = item.good_num;
           list.push(obj);
        });
        if (this.addressId !== '') {
            Indicator.open();
            this.post('home/Order/createOrder', {
              list: JSON.stringify(list),
              address_id: this.addressId,
              content: this.content,
              type: 1
            }).then(res => {
                if (res.data.code === 10000){
                  this.$refs.pop.popupVisible = true;
                  this.payInfo = res.data.info;
                }
              Indicator.close();
            })
        } else {
          Toast({
            message: '请添加收货地址',
            duration: 1000
          });
        }
    },
    back(){
      this.$router.go(-1);
    },
    selectAddress(){
      this.$router.push('/site');
    }
  },
  components:{pop},
  mounted(){
    let id = this.$route.params.id;
    this.getOrderList(id);
  }
}
</script>
<style scoped>
@import "../../../static/header.css";
@import "../../../static/goods.css";
.setOrder {
  height: 100vh;
  background-color: #f2f2f2;
}
.order_w {
    padding: 0 0.3rem;
    height: 0.88rem;
    line-height: 0.88rem;
    border-bottom: 1px solid #f2f2f2;
    background-color: #FFFFFF;
  }
.order_address img {
  width: 0.4rem;
}
.site {
  padding: 0 0.2rem;
  height: 0.88rem;
  line-height: 0.88rem;
  background-color: #FFFFFF;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #F2F2F2;
}
.site span {
  display: inline-block;
  font-size: 0.28rem;
  color: #666666;
  width: 1.4rem;
}
.site input {
  flex: 1;
  display: inline-block;
  height: 0.8rem;
  text-indent: 0.5rem;
}
.site input::placeholder {
  font-size: 0.28rem;
  color: #B2B2B2;
}
  .goods_price {
    padding: 0 0.2rem;
    height: 0.88rem;
    line-height: 0.88rem;
    background-color: #FFFFFF;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #F2F2F2;
    justify-content: space-between;
  }
  .goods_price span {
    font-size: 0.28rem;
    color: #666666;
  }
  .goods_price .price_d {
    color: #ff6f00;
  }
  .order_btn {
    height: 0.88rem;
    line-height: 0.88rem;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    background-color: #FFFFFF;
  }
  .btn_l {
    flex: 1;
    text-align: right;
  }
.btn_l span {
  font-size: 0.28rem;
}
.btn_l span:last-child{
  color: #ff6f00;
  margin-right: 0.2rem;
}
.btn_r {
    width: 25%;
    background-color: #ff6f00;
    color: #FFFFFF;
    font-size: 0.3rem;
    text-align: center;
  }
  /*收货地址*/
.order_address_t {
    display: flex;
    align-items: center;
    height: 1.2rem;
    background-color: #FFFFFF;
    padding: 0 0.3rem;
}
.order_address_t div:first-child img {
  width: 0.5rem;
}
.order_address_t img:last-child {
  width: 0.14rem;
}
.order_address_t div {
  flex: 1;
}
.order_address_t div:nth-child(2){
  flex:  0 0 80%;
}
.order_address_t div:nth-child(3){
  text-align: center;
}
.address_title {
  display: flex;
  justify-content: space-between;
}
  .address_title p {
    font-size: 0.3rem;
  }
</style>



